import { Component, Prop, h, getAssetPath, Element, State } from '@stencil/core';
import { formatDistanceToNow } from 'date-fns';

interface Review {
  author_image: string;
  author_title: string;
  review_link: string;
  review_timestamp: number;
  review_rating: number;
  review_text: string;
}

@Component({
  tag: 'reviews-widget',
  styleUrl: 'reviews-widget.css',
  assetsDirs: ['../assets'],
  shadow: true,
})
export class ReviewsWidget {
  @Prop() reviews: any[] = [];
  @Element() el: HTMLElement;
  @State() isPaused: boolean = false;
  @State() isLoading: boolean = true;

  private reviewsWrapper: HTMLElement;

  componentDidLoad() {
    this.reviewsWrapper = this.el.shadowRoot.querySelector('.reviews-wrapper');
    this.addEventListeners();

  }

  componentWillLoad() {
    this.isLoading = false;
  }


  private addEventListeners() {
    const reviews = this.el.shadowRoot.querySelectorAll('.review');
    reviews.forEach((review) => {
      review.addEventListener('mouseenter', () => this.setPaused(true));
      review.addEventListener('mouseleave', () => this.setPaused(false));
    });
  }

  private setPaused(paused: boolean) {
    this.isPaused = paused;
    this.reviewsWrapper.classList.toggle('reviews-paused', paused);
  }

  private renderStars(rating: number) {
    return Array.from({ length: rating }, (_, i) => (
      <img key={i} src={getAssetPath('../assets/icons/yellow-star.svg')} alt="Star" class="icon" />
    ));
  }

  private renderReview(review: Review, index: number) {
    return (
      <div key={index} class={`review item${index}`}>
        <div class="review-header flex items-center mb-4">
          <div class="relative">
            <img src={review.author_image} alt={review.author_title} class="w-[40px] h-[40px] rounded-full mr-2 z-0" />
            <img class="absolute left-[20px] bottom-[-5px] w-[24px]" src={getAssetPath('../assets/icons/g-logo.svg')} alt="Google Logo" />
          </div>
          <div class="flex flex-col justify-between text-[14px]">
            <p class="review-author font-bold flex items-center">
              <a href={review.review_link} target="_blank" rel="noopener noreferrer" class="no-underline text-current mr-2 truncate">
                {review.author_title}
              </a>
              <img src={getAssetPath('../assets/icons/blue-check-icon.svg')} alt="Verified" />
            </p>
            <p class="review-time text-[12px] text-gray-400">{formatDistanceToNow(new Date(review.review_timestamp * 1000))} ago</p>
          </div>
        </div>
        <div class="review-rating flex mb-2">{this.renderStars(review.review_rating)}</div>
        <p class="review-text text-base line-clamp-3 my-2">{review.review_text}</p>
        <a href={review.review_link} target="_blank" rel="noopener noreferrer" class="ml-auto mr-3 text-gray-400 no-underline text-current">
          See full review
        </a>
      </div>
    );
  }

  private renderSkeleton() {
    return Array.from({ length: 9 }).map((_, index) => (
      <div key={index} class={`review item${index} skeleton`}>
        <div class='skeleton'>
          <div class="flex">
            <div class="skeleton-avatar"></div>
            <div class="flex flex-col gap-1 flex-1 m-auto">
              <div class="skeleton-line w-[100%] "></div>
              <div class="skeleton-line w-[50%]"></div>
            </div>
          </div>
          <div class="skeleton-line w-[50%] my-3"></div>
          <div class="skeleton-content">

            <div class="skeleton-line skeleton-line w-[95%]"></div>
            <div class="skeleton-line skeleton-line w-[95%]"></div>
            <div class="skeleton-line skeleton-line w-[95%]"></div>
          </div>

          <div class="skeleton-line skeleton-line w-[70%] mt-auto"></div>
        </div>

      </div>
    ))
  }

  render() {
    return (
      <div class="reviews-widget rounded-lg mx-auto">
        <div class={`reviews-wrapper ${this.isPaused ? 'reviews-paused' : ''}`}>
          {this.isLoading
            ? this.renderSkeleton()
            : this.reviews.length > 0
              ? this.reviews.slice(0, 9).map((review, index) => this.renderReview(review, index))
              // ? this.renderSkeleton()
              : this.renderSkeleton()}
        </div>
      </div>
    );
  }
}
