:host {
  display: block;
  font-family: 'Hind Madurai', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #333;
  --animation-duration: 55s;
}

.icon {
  width: 18px;
  height: 18px;
}

.reviews-wrapper {
  width: 90%;
  max-width: 1536px;
  margin-inline: auto;
  position: relative;
  height: 300px;
  margin-top: 5rem;
  overflow: hidden;
  mask-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1) 20%, rgba(0, 0, 0, 1) 80%, rgba(0, 0, 0, 0));
}

@keyframes scrollLeft {
  to {
    left: -200px;
  }
}

.review {
  width: 200px;
  height: 220px;
  border-radius: 6px;
  position: absolute;
  left: max(calc(200px * 9), 100%);
  animation-name: scrollLeft;
  animation-duration: var(--animation-duration);
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  display: flex;
  flex-direction: column;
}

.reviews-paused .review {
  animation-play-state: paused;
}

.review-text {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.item0 {
  animation-delay: calc(var(--animation-duration) / 9 * (9 - 1) * -1);
}
.item1 {
  animation-delay: calc(var(--animation-duration) / 9 * (9 - 2) * -1);
}
.item2 {
  animation-delay: calc(var(--animation-duration) / 9 * (9 - 3) * -1);
}
.item3 {
  animation-delay: calc(var(--animation-duration) / 9 * (9 - 4) * -1);
}
.item4 {
  animation-delay: calc(var(--animation-duration) / 9 * (9 - 5) * -1);
}
.item5 {
  animation-delay: calc(var(--animation-duration) / 9 * (9 - 6) * -1);
}
.item6 {
  animation-delay: calc(var(--animation-duration) / 9 * (9 - 7) * -1);
}
.item7 {
  animation-delay: calc(var(--animation-duration) / 9 * (9 - 8) * -1);
}
.item8 {
  animation-delay: calc(var(--animation-duration) / 9 * (9 - 9) * -1);
}

.skeleton {
  display: flex;
  flex-direction: column;
  gap: 10px;
  height: 220px;
}

.skeleton-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #e0e0e0;
  margin-right: 10px;
  position: relative;
  overflow: hidden;
}

.skeleton-avatar::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: -100%;
  height: 100%;
  width: 200%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.5), transparent);
  animation: loading 1.5s infinite;
}

.skeleton-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.skeleton-line {
  height: 16px;
  border-radius: 6px;
  background-color: #e0e0e0;
  position: relative;
  overflow: hidden;
}

.skeleton-line::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: -100%;
  height: 100%;
  width: 200%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.5), transparent);
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% {
    left: -100%;
  }
  50% {
    left: 0;
  }
  100% {
    left: 100%;
  }
}
